.Projects {
    img {
        width: 500px;
    }
    .project {
        margin: 3% 10%;
        display: flex;
        justify-content: space-around;
        background: #F7D7B5;
        border-radius: 10px;
        padding: 2%;
        .project-detail {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            text-align: left;
            p {
                margin-right: 3%;
            }
        }
        .project-detail-right {
            text-align: right;
            p {
                margin-left: 3%;
                margin-right: 0;
            }
        }
    }
    h1 {
        text-decoration: underline;
        text-underline-offset: 8px;
    }
    a {
        display: block;
        text-align: center;
    }
    li {
        text-align: left;
    }
    .more-button {
        border-radius: 10px;
        margin-bottom: 2%;
    }
    .more-button:hover {
        background: #351C75;
        color: #E69138;
    }
}
@media screen and (max-width: 768px) {
    .Projects {
        .project {
            display: block;
            .project-detail {
                text-align: center;
                p {
                    margin-right: 0;
                }
            }
            .project-detail-right {
                p {
                    margin-left: 0;
                }
            }
            img {
                width: 100%;
            }
        }
        .project-right {
            display: flex;
            flex-direction: column-reverse;
            flex-wrap: wrap;
        }
    }
}