.About {
    display: flex;
    justify-content: center;
    .description {
        width: 100%;
        margin: 3% 10%;
        font-size: 20px;
        padding: 2%;
        background: #F7D7B5;
        border-radius: 10px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        border: 1px solid rgba(255, 255, 255, 0.3);
        text-align: left;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .photo {
        img {
            max-width: 300px;
            border-radius: 10px;
        }
    }
}
@media screen and (max-width: 600px) {
    .About {
        .description { 
            display: block;
            padding: 5%;
        }
        .photo {
            img {
              max-width: 100%;  
            }
        }
    }
}